import {Trans, useTranslation} from "react-i18next";
import React from "react";
import styled from "../../theme";

interface OwnProps {
    closeModal: () => void;
}

export interface StaticProps {
    labels: {
        type: string;
        header?: string;
        description: string;
        info?: string;
        confirm?: string;
        close?: string | null;
    };
}

export function MediaWarning(props: StaticProps & OwnProps ) {
    const { closeModal, labels } = props;

    const { t } = useTranslation('common');

    const { type, description = "media_unavailable", header = t`warning`, info = '', close = t`close` } = labels;

    return (
        <Container>
            <Header>{header}</Header>
            <Description>{t(type)}
                <Trans i18nKey={description}>
                    <br/> Please make sure camera is ON and trailer is ONLINE
                </Trans>
            </Description>
            <Info>{info}</Info>
            <ButtonsWrapper>
                {close !== null && <CancelButton onClick={() => closeModal()}>{close}</CancelButton>}
            </ButtonsWrapper>
        </Container>
    );
}

//FIXME Need to extract all designs of modal windows into a common code

const Container = styled.div`
  padding: 35px 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
`;

const Header = styled.div`
  font-size: 20px;
  font-weight: 600;
  line-height: 1.25;
  color: #d0021b;
  text-align: center;
  &::first-letter {
    text-transform: capitalize;
  }
`;

const Description = styled(Header)`
  margin-top: 20px;
  max-width: 405px;
  color: #000000;
  &::first-letter {
    text-transform: capitalize;
  }
`;

const Info = styled.div`
  margin-top: 15px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.25;
  color: #000000;
  text-align: center;
  &::first-letter {
    text-transform: capitalize;
  }
`;

const ButtonsWrapper = styled.div`
  margin-top: 50px;
`;

const CancelButton = styled.button`
  margin: 5px;
  padding: 8px 50px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.5;
  color: #4a90e2;
  text-align: center;
  background-color: #f4f6f8;
  border-radius: 4px;
  border: none;
  outline: none;
  cursor: pointer;
  &::first-letter {
    text-transform: capitalize;
  }
`;
